import { Component, OnInit } from '@angular/core';
import { SwitchService } from '../switch.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-step3',
  templateUrl: './step3.component.html',
  styleUrls: ['./step3.component.css']
})
export class Step3Component implements OnInit {
  currentStepObject: any = {};
  switchType: string = '';
  constructor(private router: Router, private switchService: SwitchService) { }

  ngOnInit() {
    if (this.switchService.currentUrl == "") {
      this.router.navigate(['']);
    }
    else{
      this.switchType = this.switchService.currentUrl.replace('/', '');
    }
  }

  viewMidStep() {
    this.router.navigate([this.switchType + '/usage']);
  }

  viewFirstStep() {
    this.router.navigate([this.switchType]);
  }
}
